import { of } from 'rxjs';
import { ajax } from 'rxjs/ajax';
import { map, mergeMap, catchError } from 'rxjs/operators';
import { ofType } from 'redux-observable';
import { ROLE } from '../actionTypes/role';
import { getAllRolesSuccess, changeStatusRoleSuccess, deleteRoleSuccess, addRoleSuccess, editRoleSuccess } from '../actions/role';
import API_SERVICES from '../services';

const API_GETALL = API_SERVICES.HOST + API_SERVICES.VERSION + '/Role/GetAll';
const API_CHANGE_STATUS = API_SERVICES.HOST + API_SERVICES.VERSION + '/Role/ChangeStatus';
const API_DELETE = API_SERVICES.HOST + API_SERVICES.VERSION + '/Role/Remove/';
const API_ADD = API_SERVICES.HOST + API_SERVICES.VERSION + '/Role/Create/';
const API_EDIT = API_SERVICES.HOST + API_SERVICES.VERSION + '/Role/Update';

// GETALL
const getAllRoleEpic = action$ => action$.pipe(
    ofType(ROLE.ROLE_GETALL),
    mergeMap(() => {
        return ajax.getJSON(API_GETALL, API_SERVICES.HEADERS).pipe(
            map(response => getAllRolesSuccess(response)),
            catchError(error => of({
                type: ROLE.ROLE_GETALL_FAILURE,
                payload: { message: error.xhr.response, status: error.xhr.status },
            }))
        )
    })
);

// CHANGE STATUS
const changeStatusRoleEpic = action$ => action$.pipe(
    ofType(ROLE.ROLE_CHANGE_STATUS),
    mergeMap((action) => {
        return ajax.put(API_CHANGE_STATUS, action.payload, API_SERVICES.HEADERS).pipe(
            map(response => changeStatusRoleSuccess(response)),
            catchError(error => of({
                type: ROLE.ROLE_CHANGE_STATUS_FAILURE,
                payload: { message: error.xhr.response, status: error.xhr.status },
            }))
        )
    })
);

// DELETE
const deleteRoleEpic = action$ => action$.pipe(
    ofType(ROLE.ROLE_DELETE),
    mergeMap((action) => {
        return ajax.delete(API_DELETE + action.payload, API_SERVICES.HEADERS).pipe(
            map(response => deleteRoleSuccess(response)),
            catchError(error => of({
                type: ROLE.ROLE_DELETE_FAILURE,
                payload: { message: error.xhr.response, status: error.xhr.status },
            }))
        )
    })
);

// ADD
const addRoleEpic = action$ => action$.pipe(
    ofType(ROLE.ROLE_ADD),
    mergeMap((action) => {
        return ajax.post(API_ADD + action.payload, null, API_SERVICES.HEADERS).pipe(
            map(response => addRoleSuccess(response)),
            catchError(error => of({
                type: ROLE.ROLE_ADD_FAILURE,
                payload: { message: error.xhr.response, status: error.xhr.status },
            }))
        )
    })
);

// EDIT
const editRoleEpic = action$ => action$.pipe(
    ofType(ROLE.ROLE_EDIT),
    mergeMap((action) => {
        return ajax.put(API_EDIT + action.payload, null, API_SERVICES.HEADERS).pipe(
            map(response => editRoleSuccess(response)),
            catchError(error => of({
                type: ROLE.ROLE_EDIT_FAILURE,
                payload: { message: error.xhr.response, status: error.xhr.status },
            }))
        )
    })
);

export { getAllRoleEpic, changeStatusRoleEpic, deleteRoleEpic, addRoleEpic, editRoleEpic };